import { Button, Form, Input, message } from 'antd';
import React, { useEffect, useRef, useState } from 'react';
import TypewriterExample from '../../Typing';

const DoanSO = ({ setWin }) => {
  const [isTypingComplete, setIsTypingComplete] = useState(false);
  const [noti, setNoti] = useState({
    noti: 'Trong th thch ny chng trnh s random ra mt s ngu nhin t 1-100 nhim v c bn l tm ra c con s ny bn c 6 ln on mi ln on chng trnh s cho bit s bn on ln hn hay b hn s cn tim',
    count: 6,
    QKold: [1, 100],
  });
  const [reset, setReset] = useState(false);
  const KQ = useRef(Math.floor(Math.random() * 100 + 1));
  const [btnWin, setBtnWin] = useState(false);
  console.log(KQ);
  const onFinish = (values) => {
    if (noti.count === 1 && Number(values.num) !== KQ.current) {
      setNoti({
        noti: 'Bn  thua cuc! Hy n reset  th li nha <3',
        count: noti.count,
        QKold: noti.QKold - 1,
      });

      setReset(true);
    } else if (values.num > KQ.current) {
      setNoti({
        noti:
          'Con s ca bn ln hn p n! p n nm trong khong: ' +
          noti.QKold[0] +
          '-' +
          values.num,
        count: noti.count - 1,
        QKold: [noti.QKold[0], values.num],
      });
    } else if (values.num < KQ.current) {
      setNoti({
        noti:
          'S ' +
          values.num +
          ' nh hn p n! p an nm trong khong: ' +
          values.num +
          '-' +
          noti.QKold[1],
        count: noti.count - 1,
        QKold: [values.num, noti.QKold[1]],
      });
    } else {
      setNoti({
        noti: 'Xin chc mng H  tr li chnh xc',
        count: noti.count - 1,
        QKold: [KQ, KQ],
      });
      setIsTypingComplete(false);
      setBtnWin(true);
    }
  };
  const onReset = () => {
    KQ.current = Math.floor(Math.random() * 101);

    setNoti({
      noti: 'Tip: bn hy chn nhng con s chnh gia  th VD:1-100 hy chn s 50',
      count: 6,
      QKold: [1, 100],
    });
    setReset(false);
  };
  return (
    <div style={{ fontSize: 20 }}>
      <TypewriterExample
        data={noti.noti}
        setIsTypingComplete={setIsTypingComplete}
      />
      <Button
        style={btnWin ? {} : { display: 'none' }}
        onClick={() => setWin()}
      >
        OK
      </Button>
      <div style={isTypingComplete ? {} : { display: 'none' }}>
        <div>S lt cn li ca bn:{noti.count} </div>

        <Form onFinish={onFinish}>
          <Form.Item
            label="Hy nhp con s bn mun on vo y"
            name={'num'}
            rules={[
              {
                required: true,
                message: 'Khng c  trng!',
              },
            ]}
          >
            <Input type="number" />
          </Form.Item>
          <Form.Item>
            <Button style={!reset ? {} : { display: 'none' }} htmlType="submit">
              Kim tra
            </Button>
            <Button
              style={reset ? {} : { display: 'none' }}
              htmlType="button"
              onClick={onReset}
            >
              Reset
            </Button>
          </Form.Item>
        </Form>
      </div>
    </div>
  );
};

export default DoanSO;
